</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
            uObject: null,
			type: '',
			
			
			ro:{
				cheweibianhao : false,
				cheweimingcheng : false,
				cheweileixing : false,
				cheweitupian : false,
				cheweiweizhi : false,
				shoufeibiaozhun : false,
				tingchejiage : false,
				zhanghao : false,
				xingming : false,
				chepaihaoma : false,
				shouji : false,
				ruchangshijian : false,
				chuchangshijian : false,
				zongshizhang : false,
				hejifeiyong : false,
				dengjiriqi : false,
				jifen : false,
				ispay : false,
			},
			
			
			ruleForm: {
				cheweibianhao: '',
				cheweimingcheng: '',
				cheweileixing: '',
				cheweitupian: '',
				cheweiweizhi: '',
				shoufeibiaozhun: '',
				tingchejiage: '',
				zhanghao: '',
				xingming: '',
				chepaihaoma: '',
				shouji: '',
				ruchangshijian: '',
				chuchangshijian: '',
				zongshizhang: '',
				hejifeiyong: '',
				dengjiriqi: '',
				jifen: '',
			},
		

			
			rules: {
				cheweibianhao: [
				],
				cheweimingcheng: [
				],
				cheweileixing: [
				],
				cheweitupian: [
				],
				cheweiweizhi: [
				],
				shoufeibiaozhun: [
				],
				tingchejiage: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				zhanghao: [
				],
				xingming: [
				],
				chepaihaoma: [
				],
				shouji: [
					{ validator: validateMobile, trigger: 'blur' },
				],
				ruchangshijian: [
				],
				chuchangshijian: [
				],
				zongshizhang: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				hejifeiyong: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				dengjiriqi: [
				],
				jifen: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				ispay: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {
		zongshizhang : {
			get: function () {
				let h = this.ruleForm
				let a = 'h.ruchangshijian-h.chuchangshijian'
				let n = a.split('-')
				let hour = this.getHour(h[n[0].split('h.')[1]], h[n[1].split('h.')[1]])
				this.ruleForm.zongshizhang = hour?hour:0
				return hour?hour:0
			}
			
		},
		hejifeiyong : {
			get: function () {
				let c = this.ruleForm
				let a = c.tingchejiage*c.zongshizhang
				this.ruleForm.hejifeiyong = a?Number(a).toFixed(2):0
				return a?Number(a).toFixed(2):0
			}
			
		},
		jifen: {
			get: function () {
				return 0+parseFloat(this.ruleForm.hejifeiyong==""?0:this.ruleForm.hejifeiyong) || 0
			}
		},



	},
    components: {
    },
	created() {
		this.ruleForm.chuchangshijian = this.getCurDateTime()
		this.ruleForm.dengjiriqi = this.getCurDate()
	},
	methods: {
		// 获取时间间隔 单位小时
		getHour(first, last){
			let date1 = new Date(first)
			let date2 = new Date(last)
			let a = date1.getTime();
			let b = date2.getTime();
			var count = 0;
			for (var i = a; i < b; i += 3600 * 1000) {
				count++;
			}
			return count;
		},
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='cheweibianhao'){
							this.ruleForm.cheweibianhao = obj[o];
							this.ro.cheweibianhao = true;
							continue;
						}
						if(o=='cheweimingcheng'){
							this.ruleForm.cheweimingcheng = obj[o];
							this.ro.cheweimingcheng = true;
							continue;
						}
						if(o=='cheweileixing'){
							this.ruleForm.cheweileixing = obj[o];
							this.ro.cheweileixing = true;
							continue;
						}
						if(o=='cheweitupian'){
							this.ruleForm.cheweitupian = obj[o];
							this.ro.cheweitupian = true;
							continue;
						}
						if(o=='cheweiweizhi'){
							this.ruleForm.cheweiweizhi = obj[o];
							this.ro.cheweiweizhi = true;
							continue;
						}
						if(o=='shoufeibiaozhun'){
							this.ruleForm.shoufeibiaozhun = obj[o];
							this.ro.shoufeibiaozhun = true;
							continue;
						}
						if(o=='tingchejiage'){
							this.ruleForm.tingchejiage = obj[o];
							this.ro.tingchejiage = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
						if(o=='chepaihaoma'){
							this.ruleForm.chepaihaoma = obj[o];
							this.ro.chepaihaoma = true;
							continue;
						}
						if(o=='shouji'){
							this.ruleForm.shouji = obj[o];
							this.ro.shouji = true;
							continue;
						}
						if(o=='ruchangshijian'){
							this.ruleForm.ruchangshijian = obj[o];
							this.ro.ruchangshijian = true;
							continue;
						}
						if(o=='chuchangshijian'){
							this.ruleForm.chuchangshijian = obj[o];
							this.ro.chuchangshijian = true;
							continue;
						}
						if(o=='zongshizhang'){
							this.ruleForm.zongshizhang = obj[o];
							this.ro.zongshizhang = true;
							continue;
						}
						if(o=='hejifeiyong'){
							this.ruleForm.hejifeiyong = obj[o];
							this.ro.hejifeiyong = true;
							continue;
						}
						if(o=='dengjiriqi'){
							this.ruleForm.dengjiriqi = obj[o];
							this.ro.dengjiriqi = true;
							continue;
						}
						if(o=='jifen'){
							this.ruleForm.jifen = obj[o];
